.map-container-starmap {
  display: table-cell;
  vertical-align: middle;
  height: 410px;
}

#map {
  width: auto;
  height: auto;
}

.svg > svg {
  height: 40px;
  width: 40px;
  cursor: pointer;
}

.svg-otl > svg {
  outline: 1px solid #000;
  outline-offset: 2px;
}

.cookiesContent {
  width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  color: #000;
  text-align: center;
  border-radius: 20px;
  padding: 30px 30px 70px;
}

button.close {
  width: 30px;
  font-size: 20px;
  color: #c0c5cb;
  align-self: flex-end;
  background-color: transparent;
  border: none;
  margin-bottom: 10px;
}
img {
  width: 82px;
  margin-bottom: 15px;
}
p {
  margin-bottom: 40px;
  font-size: 18px;
}
button.accept {
  background-color: #ed6755;
  border: none;
  border-radius: 5px;
  width: 200px;
  padding: 14px;
  font-size: 16px;
  color: white;
  box-shadow: 0px 6px 18px -5px rgba(237, 103, 85, 1);
}
